import { Department, EmployeeRole } from "generated/prisma";
import { CreateEmployeeDto } from "../types/dtos/employee.dto";

export type EmployeeForm = {
  fullName: string;
  phone: string;
  email: string;
  department: Department | null;
  role: EmployeeRole | null;
};

export const getDefaultEmployeeForm = (): EmployeeForm => ({
  fullName: '',
  phone: '',
  email: '',
  department: null,
  role: null
});

export const convertEmployeeFormToDto = (form: EmployeeForm): CreateEmployeeDto => ({
  fullName: form.fullName.trim(),
  phone: form.phone.trim() || null,
  email: form.email.trim() || null,
  departmentId: form.department?.id || null,
  roleId: form.role?.id || null
});